// COLORS
$color-primary: #5B7F95;
$color-primary-hover: #DEE5EA;
$color-secondary: #BF8B85;
$color-secondary-hover: #EAE0DF;
$color-tertiary: #D3C0AB;


$color-text-dark: #222222;
$color-text-light: #FAFAFA;
$color-text-faint: #949494;
$color-text-backgound-light: #F7F8F9;
$color-text-backgound-light: #edeef0;

$color-error: #B00000;


$border-radius: 3px;

$border-radius: 3px;
$gap: var(--space-xl);
$gap-small: var(--space-m);
$padding-inline-button: 2.4rem;
$padding-block-button: 1rem;

$box-shadow: 2px 2px 2px 0px darken($color-text-light, 5);
//font
$font-family-default: 'Montserrat', sans-serif;
$font-size-default: var(--step-1);

$filter-light: invert(99%) sepia(91%) saturate(30%) hue-rotate(181deg) brightness(10001%) contrast(200%);
$filter-dark: invert(2%) sepia(2%) saturate(6271%) hue-rotate(200deg) brightness(89%) contrast(81%);

/* @link https://utopia.fyi/space/calculator?c=320,14,1.2,1240,32,1.25,5,2,&s=0.75|0.5|0.25,1.5|2|3|4|6,&g=s,l,xl,12 */

:root {
    --space-3xs: clamp(0.25rem, calc(0.16rem + 0.43vw), 0.50rem);
    --space-2xs: clamp(0.44rem, calc(0.24rem + 0.98vw), 1.00rem);
    --space-xs: clamp(0.69rem, calc(0.40rem + 1.41vw), 1.50rem);
    --space-s: clamp(0.88rem, calc(0.48rem + 1.96vw), 2.00rem);
    --space-m: clamp(1.31rem, calc(0.73rem + 2.93vw), 3.00rem);
    --space-l: clamp(1.75rem, calc(0.97rem + 3.91vw), 4.00rem);
    --space-xl: clamp(2.63rem, calc(1.45rem + 5.87vw), 6.00rem);
    --space-2xl: clamp(3.50rem, calc(1.93rem + 7.83vw), 8.00rem);
    --space-3xl: clamp(5.25rem, calc(2.90rem + 11.74vw), 12.00rem);

    /* One-up pairs */
    --space-3xs-2xs: clamp(0.25rem, calc(-0.01rem + 1.30vw), 1.00rem);
    --space-2xs-xs: clamp(0.44rem, calc(0.07rem + 1.85vw), 1.50rem);
    --space-xs-s: clamp(0.69rem, calc(0.23rem + 2.28vw), 2.00rem);
    --space-s-m: clamp(0.88rem, calc(0.14rem + 3.70vw), 3.00rem);
    --space-m-l: clamp(1.31rem, calc(0.38rem + 4.67vw), 4.00rem);
    --space-l-xl: clamp(1.75rem, calc(0.27rem + 7.39vw), 6.00rem);
    --space-xl-2xl: clamp(2.63rem, calc(0.76rem + 9.35vw), 8.00rem);
    --space-2xl-3xl: clamp(3.50rem, calc(0.54rem + 14.78vw), 12.00rem);

    /* Custom pairs */
}

.margin-bottom-m {
    margin-bottom: (var(--space-m));
}


.btn {
    font-family: inherit;
    text-decoration: none;
    cursor: pointer;
    transition: all .25s;
    font-family: inherit;
    font-size: $font-size-default;
    border-radius: $border-radius;


    &:hover {
        transform: translateY(-2px);
    }

    &--primary {
        background-color: $color-primary;
        padding: 1rem 3rem;
        color: $color-text-light !important;

        &:hover {
            background-color: $color-primary-hover;
            color: $color-text-dark !important;

            >.icon {
                filter: $filter-dark;
            }
        }

        >.icon {
            filter: $filter-light;
        }
    }

    &--secondary {
        background-color: $color-secondary;
        padding: 1rem 3rem;
        color: $color-text-light;

        &:hover {
            background-color: $color-secondary-hover;
            color: $color-text-dark;
        }
    }

    &--intext {
        border: none;
        color: $color-secondary;
        background: transparent;

    }
}